body {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  background-color: #B18C6F; }

.user {
  margin: -5.5em 2em 0 0;
  text-align: center;
  float: right; }

.btn {
  height: 2em;
  width: 15em;
  margin: 0.5em 0 0.5em 0;
  border: 2px solid #C69C72; }

.btn:hover {
  cursor: pointer; }

.user-input {
  width: 15em; }

.primary-btn {
  background-color: #44A8FF; }

.secondary-btn {
  background-color: #C0B7B1;
  -webkit-transition: 0.5s ease all;
  -o-transition: 0.5s ease all;
  transition: 0.5s ease all; }

.secondary-btn:hover {
  background-color: #44A8FF; }

.register-btn {
  -ms-flex-item-align: center;
      align-self: center; }

.align-center {
  text-align: center; }

.filter form {
  display: inline;
  margin: 0 5em 0 5em; }

.search-btn {
  height: 2em;
  width: 10em;
  margin: -0.4em 0 0 0;
  border: 2px solid #C69C72; }

.search-btn {
  cursor: pointer; }

.search-input {
  margin: 0 0.2em 0 1em;
  height: 1.4em; }

.radio-btn {
  margin-top: 2em; }

html {
  padding: 0;
  margin: 0; }

header {
  background-color: #433E3F;
  color: #C0B7B1; }

.heading {
  font-family: 'Permanent Marker', cursive;
  font-style: italic;
  font-size: 3em;
  margin-top: -0.15em; }

.header {
  width: 100%;
  padding-top: 0; }

.header h1 {
  margin-left: 0.5em;
  padding-top: 0.3em; }

.filter {
  padding: 1em 0 1em 0;
  border-top: 2px solid #C0B7B1;
  background-color: #7A7265;
  margin: -1em 0 0 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.filter .radio-btn {
  padding-top: 0.5em; }

.posts {
  color: #433E3F;
  margin: 2em 10% 0 10%;
  border: 2px solid #433E3F;
  border-radius: 0.3em;
  /*box-shadow: 0px 0px 5px 5px #74C658;*/
  padding: 0 0 0.5em 2em; }

hr {
  border-color: #433E3F; }

.new-post {
  margin: 1rem 0 5rem 0;
  width: 23em;
  height: 3em; }

.create-post {
  text-align: left;
  margin: 2em 40% 0 41.5%; }

.form-control-area {
  width: 35em;
  height: 10em; }

.post {
  padding: 0 8em 0 0; }

.post p {
  word-break: break-word;
  display: inline; }

.vote-count {
  margin-top: 1em; }

.upvote-count {
  color: #128D12; }

.vote-container {
  margin-top: 0.5em; }

.vote-btn {
  background-color: #C0B7B1;
  height: 2em;
  width: 7em;
  margin: 0.5em 0 0 0;
  -webkit-transition: 0.3s ease all;
  -o-transition: 0.3s ease all;
  transition: 0.3s ease all; }

.upvote {
  border: 2px solid #47C547 !important; }

.upvote:hover {
  background-color: #47C547; }

.unvote {
  border: 2px solid #EC4343; }

.unvote:hover {
  background-color: #EC4343; }

.remove {
  border: 2px solid #EC4343; }

.remove:hover {
  background-color: #EC4343; }

.error {
  font-weight: bold; }

.footer {
  text-align: center;
  width: 100%;
  position: fixed;
  background-color: #393939;
  bottom: 0;
  margin-top: 60px; }

.footer a {
  color: white;
  text-decoration: none; }

.footer ul {
  list-style-type: none; }

.footer ul li {
  margin: 0 2em 0 2em;
  display: inline; }

@media (max-width: 1200px) {
  .filter {
    -ms-flex-align: left;
        align-items: left;
    -ms-flex-direction: column;
        flex-direction: column; }

  .filter form {
    margin: 0.4em 0 0.4em 2em; }

  .checkbox-container {
    text-align: left; } }
@media (max-width: 860px) {
  .create-post {
    text-align: left;
    margin: 2em 40% 0 32.5%; } }



